import { z } from 'zod';
import { Planogram } from '@/lib/generated/api-schemas';

const LayoutItemSchema = z.object({
  i: z.string(),
  x: z.number(),
  y: z.number(),
  w: z.number(),
  h: z.number(),
  meta: z.object({
    id: z.number(),
    name: z.string(),
    category: z.string(),
    color: z.string().optional(),
    score: z.number(),
    pack_width_in: z.number(),
    pack_height_in: z.number(),
  }),
});

const GridResponseSchema = z.object({
  grid: z.object({
    cols: z.number(),
    rows: z.number(),
    cellWidthIn: z.number(),
  }),
  rows: z.array(
    z.object({
      id: z.number(),
      category: z.string().nullable(),
      name: z.string(),
      items: z.array(LayoutItemSchema),
    })
  ),
});

export const PlanogramDetailResponseSchema = z.object({
  planogram: Planogram,
  layout: GridResponseSchema.optional(),
});

export type PlanogramDetailResponse = z.infer<typeof PlanogramDetailResponseSchema>;
